<template>
    <div class="m-archive-box" v-loading="loading">
        <div class="m-plugins-header">
            <h1 class="m-plugins-title">
                <i :class="subtype | showIcon"></i>{{ subtype | showLabel }}
            </h1>
        </div>

        <!-- ÊêúÁ¥¢ -->
        <div class="m-archive-search" slot="search-before">
            <a :href="publish_link" class="u-publish el-button el-button--primary">+ ÂèëÂ∏É‰ΩúÂìÅ</a>
            <el-input placeholder="ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ" v-model.trim.lazy="search">
                <span slot="prepend">ÂÖ≥ÈîÆËØç</span>
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
        </div>

        <!-- Á≠õÈÄâ -->
        <div class="m-archive-filter">
            <!-- ÁâàÊú¨ËøáÊª§ -->
            <clientBy @filter="filterImperceptibly" :type="client"></clientBy>
            <!-- ËßíÊ†áËøáÊª§ -->
            <markBy @filter="filterMeta"></markBy>
            <!-- ÊéíÂ∫èËøáÊª§ -->
            <orderBy @filter="filterMeta"></orderBy>
        </div>

        <!-- ÂàóË°® -->
        <div class="m-archive-list" v-if="data && data.length">
            <ul class="u-list">
                <list-item v-for="(item, i) in data" :key="i + item" :item="item" :order="order" />
            </ul>
        </div>

        <!-- Á©∫ -->
        <el-alert v-else class="m-archive-null" title="Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥Êù°ÁõÆ" type="info" center show-icon></el-alert>

        <!-- ‰∏ã‰∏ÄÈ°µ -->
        <el-button class="m-archive-more" v-show="hasNextPage" type="primary" @click="appendPage" :loading="loading" icon="el-icon-arrow-down">Âä†ËΩΩÊõ¥Â§ö</el-button>

        <!-- ÂàÜÈ°µ -->
        <el-pagination
            class="m-archive-pages"
            background
            layout="total, prev, pager, next, jumper"
            :hide-on-single-page="true"
            :page-size="per"
            :total="total"
            :current-page.sync="page"
            @current-change="changePage"
        ></el-pagination>
    </div>
</template>
<script>
import { appKey } from "@/../setting.json";
import listItem from "@/components/list/list_item.vue";
import { publishLink } from "@jx3box/jx3box-common/js/utils";
import { getPosts } from "@/service/post";
import { jx3dat_types,jx3dat_icons } from "@/assets/data/types.json";
export default {
    name: "Index",
    props: [],
    data: function() {
        return {
            loading: false, //Âä†ËΩΩÁä∂ÊÄÅ
            data: [], //Êï∞ÊçÆÂàóË°®

            page: 1, //ÂΩìÂâçÈ°µÊï∞
            per: 10, //ÊØèÈ°µÊù°ÁõÆ
            total: 1, //ÊÄªÊù°ÁõÆÊï∞
            pages: 1, //ÊÄªÈ°µÊï∞
            number_queries: ["per", "page"],

            subtype: this.$store.state.client == 'std' ? '1' : '5', //Â≠êÁ±ªÂà´
            order: "update", //ÊéíÂ∫èÊ®°Âºè
            mark: "", //Á≠õÈÄâÊ®°Âºè
            client: this.$store.state.client, //ÁâàÊú¨ÈÄâÊã©
            search: "", //ÊêúÁ¥¢Â≠ó‰∏≤
        };
    },
    computed: {
        // ÂèëÂ∏ÉÊåâÈíÆÈìæÊé•
        publish_link: function() {
            return publishLink(appKey);
        },
        // ÊòØÂê¶ÊòæÁ§∫Âä†ËΩΩÊõ¥Â§ö
        hasNextPage: function() {
            return this.pages > 1 && this.page < this.total;
        },
        // ËØ∑Ê±ÇÂÖ≥ËÅîÂèÇÊï∞
        query: function() {
            return {
                subtype: this.subtype,
                order: this.order,
                mark: this.mark,
                client: this.client,
                search: this.search,
            };
        },
        // ÂàÜÈ°µÁõ∏ÂÖ≥ÂèÇÊï∞
        pg_queries : function (){
            return {
                page: this.page,
                per: this.per,
            }
        },
        // ÈáçÁΩÆÈ°µÁ†ÅÂèÇÊï∞
        reset_queries: function() {
            return [this.subtype, this.search];
        },
    },
    methods: {
        // ÊûÑÂª∫ÊúÄÁªàËØ∑Ê±ÇÂèÇÊï∞
        buildQuery: function(appendMode) {
            if (appendMode) {
                this.page += 1
            }
            let _query = {
                per : this.per,
                page : this.page,
            };

            for (let key in this.query) {
                if (this.query[key] !== undefined && this.query[key] !== "" && this.query[key] !== null) {
                    if (key == "search") {
                        _query.search = this.query.search.trim();
                    } else {
                        _query[key] = this.query[key];
                    }
                }
            }
            // ÂΩìÊåáÂÆöÂ≠êÁ±ªÂà´Êó∂ÂêØÁî®ÁΩÆÈ°∂
            if(_query.subtype){
                _query.sticky = 1
            }
            
            return _query;
        },
        // Âä†ËΩΩÊï∞ÊçÆ
        loadData: function(appendMode = false) {
            let query = this.buildQuery(appendMode);
            console.log("[cms-list]", "<loading data>", query);

            this.loading = true;
            return getPosts(query)
                .then((res) => {
                    if (appendMode) {
                        this.data = this.data.concat(res.data?.data?.list);
                    } else {
                        this.data = res.data?.data?.list;
                    }
                    this.total = res.data?.data?.total;
                    this.pages = res.data?.data?.pages;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        // Ë∑ØÁî±ÁªëÂÆö
        replaceRoute: function(extend) {
            return this.$router
                .push({ name: this.$route.name, query: Object.assign({}, this.$route.query, extend) })
                .then(() => {
                    window.scrollTo(0, 0);
                })
                .catch((err) => {});
        },
        // Êù°‰ª∂ËøáÊª§
        filterMeta: function(o) {
            this.replaceRoute({ [o["type"]]: o["val"], page: 1 });
        },
        // Êù°‰ª∂ËøáÊª§Ôºà‰∏çÈôÑÂä†Ë∑ØÁî±Ôºâ
        filterImperceptibly: function(o) {
            this[o["type"]] = o["val"];
        },
        // ÁøªÈ°µÂä†ËΩΩ
        changePage: function(i) {
            this.loadData();
            this.replaceRoute({ page: i });
        },
        // ËøΩÂä†Âä†ËΩΩ
        appendPage: function() {
            this.loadData(true);
        },
    },
    watch: {
        // Âä†ËΩΩË∑ØÁî±ÂèÇÊï∞
        "$route.query": {
            deep: true,
            immediate: true,
            handler: function(query) {
                if (Object.keys(query).length) {
                    console.log("[cms-list]", "<route query change>", query);
                    for (let key in query) {
                        // for:elementÂàÜÈ°µÁªÑ‰ª∂ÂÖºÂÆπÊÄßÈóÆÈ¢ò
                        if (this.number_queries.includes(key)) {
                            this[key] = ~~query[key];
                        } else {
                            this[key] = query[key];
                        }
                    }
                }
            },
        },
        // ÈáçÁΩÆÂàÜÈ°µÂèÇÊï∞
        reset_queries: {
            deep: true,
            handler: function() {
                console.log("[cms-list]", "<reset page>");
                this.page = 1;
            },
        },
        // ÁõëÂê¨ËØ∑Ê±ÇÂèÇÊï∞
        query: {
            deep: true,
            immediate: true,
            handler: function(query) {
                console.log("[cms-list]", "<request query change>", query);
                this.loadData();
            },
        },
    },
    filters :{
        showIcon: function(subtype) {
            return jx3dat_icons[subtype];
        },
        showLabel: function(subtype) {
            return jx3dat_types[subtype];
        },
    },
    mounted: function() {},
    components: {
        listItem,
    },
};
</script>

<style lang="less">
@import "~@/assets/css/list.less";
@import "~@/assets/css/plugins.less";
</style>
